<template>
  <div class="barrages-drop">
    <vue-baberrage
      :isShow="barrageIsShow"
      :barrageList="barrageList"
      :throttleGap="throttleGap"
      :loop="barrageLoop"
      :boxHeight="boxHeight"
      :lanesCount="lanesCount"
      :messageHeight="messageHeight"
    >
    </vue-baberrage>
  </div>
</template>
<script>
export default {
  name: "Barrages",
  props: {
    list: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      lanesCount: 2,
      barrageIsShow: true,
      messageHeight: 50,
      boxHeight: 200,
      boxWidth: 200,
      barrageLoop: true,
      throttleGap: 1000,
      barrageList: [],
    };
  },
  mounted() {
    this.addToList();
  },
  methods: {
    addToList() {
      this.list.forEach((v) => {
        this.barrageList.push({
          id: v.id,
          avatar: v.avatar,
          msg: v.msg,
          time: v.time || 10,
          type: v.type,
        });
      });
    },
  },
};
</script>
<style scoped>
>>> .baberrage-lane {
  display: flex;
  height: 0px;
  margin-bottom: 5px;
}
>>> .baberrage-stage {
  height: 100px;
  padding-top: 22px;
  position: absolute;
  top: 280px;
}
>>> .baberrage-item {
  position: relative;
  width: fit-content;
  padding: 0;
  border-radius: 20px;
}
>>> .baberrage-item .baberrage-msg {
  color: #fff !important;
  font-size: 10px;
  padding: 0 5px;
}
>>> .baberrage-item .baberrage-avatar img {
  width: 24px;
  height: 24px;
  margin-top: 3px;
}
>>> .baberrage-stage .baberrage-lane:last-child {
  margin-bottom: 10px;
}
>>> .normal {
  background: #1f263b !important;
}
</style>